<template>
  <div>
    <div class="box">{{ showNumber }}</div>
    <div class="circle">{{ showArithmetic }}</div>
    <table align="center">
          <tr>
              <td><button @click="clear()">C</button></td>
              <td><button @click="clearAll()">CA</button></td>
              <td><button @click="chooseArithmetic('%');">%</button></td>
              <td><button @click="chooseArithmetic('×');">×</button></td>
          </tr>
          <tr>
              <td><button @click="chooseNumber('7');">7</button></td>
              <td><button @click="chooseNumber('8');">8</button></td>
              <td><button @click="chooseNumber('9');">9</button></td>
              <td><button @click="chooseArithmetic('÷');">÷</button></td>
          </tr>
          <tr>
              <td><button @click="chooseNumber('4');">4</button></td>
              <td><button @click="chooseNumber('5');">5</button></td>
              <td><button @click="chooseNumber('6');">6</button></td>
              <td><button @click="chooseArithmetic('-');">-</button></td>
          </tr>
          <tr>
              <td><button @click="chooseNumber('1');">1</button></td>
              <td><button @click="chooseNumber('2');">2</button></td>
              <td><button @click="chooseNumber('3');">3</button></td>
              <td><button @click="chooseArithmetic('+');">+</button></td>
          </tr>
          <tr>
              <td><button @click="chooseNumber('0');">0</button></td>
              <td><button @click="chooseNumber('.');">.</button></td>
              <td><button @click="calculationResult();">=</button></td>
          </tr>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      chooseNumberArray: [],
      chooseArithmeticArray: [],
      inputNumberFlg: true,
      calculateNumberFlg: false,
      selectedArithmetic: "",
      selectedNumber: "0"
    };
  },
  computed: {
    showNumber() {
      let showNumber;
      if (this.inputNumberFlg) {
        var selectNumber = String(this.selectedNumber);
        showNumber = selectNumber.replace(/^0{1,}([^.])/, "$1");
      } else {
        showNumber = this.chooseNumberArray.reduce((prevNumber, currenNumber, index, array) => {
          return this.calculateNumber(prevNumber, currenNumber, index - 1);
        });
      }
        return Number(showNumber).toLocaleString(undefined, {maximumFractionDigits: 20});
    },
    showArithmetic() {
      if (this.selectedArithmetic) return this.selectedArithmetic;
      return "";
    },
    showPrevNumber() {
      if (this.chooseNumberArray.length > 0) return String(this.chooseNumberArray).replace(/^0{1,}([^.])/, "$1");
      return "";
    }
  },
  methods: {
    // 計算処理
    calculateNumber(prevNumber, currenNumber, index) {
      switch (this.chooseArithmeticArray[index]) {
        case "+":
          return prevNumber + currenNumber;
          break;
        case "-":
          return prevNumber - currenNumber;
          break;
        case "÷":
          return prevNumber / currenNumber;
          break;
        case "×":
          return prevNumber * currenNumber;
          break;
        case "%":
          return prevNumber % currenNumber;
          break;
      }
    },
    // 計算結果出力押下時
    calculationResult() {
      if (this.inputNumberFlg && this.selectedArithmetic && !this.calculateNumberFlg) {
        this.chooseNumberArray.push(Number(this.selectedNumber));
        this.selectedArithmetic = "";
        this.selectedNumber = this.chooseNumberArray.reduce((prev, next, index, array) => {
          return this.calculateNumber(prev, next, index - 1);
        });
        this.chooseNumberArray = [];
        this.chooseArithmeticArray = [];
        this.calculateNumberFlg = true;
      }
    },
    // 数字押下時
    chooseNumber(number) {
      if (number === '.' && this.selectedNumber.indexOf('.') >= 0) {
        return;
      }
      if (this.calculateNumberFlg) {
        this.selectedNumber = "0";
        this.calculateNumberFlg = false;
      }
      if (!this.inputNumberFlg) {
        this.chooseArithmeticArray.push(this.selectedArithmetic);
        this.selectedNumber = "0";
        this.inputNumberFlg = true;
      }
      this.selectedNumber += number;
    },
    // 四則演算記号押下時
    chooseArithmetic(arithmetic) {
      if (this.inputNumberFlg) {
        this.chooseNumberArray.push(Number(this.selectedNumber));
        this.inputNumberFlg = false;
      }
      this.selectedArithmetic = arithmetic;
    },
    // Clear押下時
    clear() {
      if (this.selectedNumber === "0") return;
      this.selectedNumber = this.selectedNumber.slice(0, -1);
    },
    // ClearAll押下時
    clearAll() {
      this.chooseNumberArray = [];
      this.chooseArithmeticArray = [];
      this.selectedNumber = "0";
      this.selectedArithmetic = "";
      this.inputNumberFlg = true;
    }
  }
};
</script>

<style lang="scss" scoped>
button {
	height: 41px;
	min-width: 60px;
	padding: 0 10px;
	margin: 5px 5px;
	background: #EFF0F2;
	border-radius: 4px;
	border-top: 1px solid #F5F5F5;
	box-shadow: 0 0 25px #E8E8E8 inset, 0 1px 0 #C3C3C3, 0 2px 0 #C9C9C9, 0 2px 3px #333333;
	color: #AAAAAA;
	text-shadow: 0 1px 0 #F5F5F5;
	font: bold 14px arial;
	text-align: center;
	line-height: 41px;
	display: inline-block;
}

button:active {
    box-shadow: inset 0 0 2px rgba(128, 128, 128, 0.1);
    transform: translateY(2px);
}

.box {
  position: relative;
  margin: 0.5em auto;
  padding: 10px 10px 5px;
  width: 90%;
  background: #fff;
  color: #555555;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 2px #ccc;
  text-align: right;
  font-size: 28px;
}

.circle{
  display: inline-block;
  text-decoration: none;
  color: #525253;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  border: solid 2px #cdcdce;
  text-align: center;
  vertical-align: middle;
  overflow: hidden;
  font-weight: bold;
  transition: .4s;
  font-size: 20px;
}

</style>


